body {
  margin: 0;
  padding: 0;
  font-family: 'Indie Flower', cursive;
  font-size: 10px;
  width: 100%;
  height: 100%;
  background: hsl(55, 50%, 65%);
  background: -webkit-gradient(linear, left top, left bottom, from(hsl(55, 55%, 65%)), to(hsl(55, 45%, 50%))) fixed;
  background: -webkit-linear-gradient(hsl(55, 55%, 65%), hsl(55, 45%, 50%)) fixed;
  background: -o-linear-gradient(hsl(55, 55%, 65%), hsl(55, 45%, 50%)) fixed;
  background: linear-gradient(hsl(55, 55%, 65%), hsl(55, 45%, 50%)) fixed;
}

.root {
  width: 100%;
  height: 100%;
  position: relative;
}



