.App {
  text-align: center;
  width: 75vmin;
  height: 75vmin;
  min-width: 55em;
  min-height: 55em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: hsl(100, 30%, 45%);
  background: -webkit-radial-gradient(circle, hsl(100, 50%, 55%), hsl(100, 30%, 45%), hsl(100, 30%, 35%));
  background: -o-radial-gradient(circle, hsl(100, 50%, 55%), hsl(100, 30%, 45%), hsl(100, 30%, 35%));
  background: radial-gradient(circle, hsl(100, 50%, 55%), hsl(100, 30%, 45%), hsl(100, 30%, 35%));
  border: 2em hsl(40, 50%, 50%) solid;
  -webkit-border-image: -webkit-radial-gradient(circle, hsl(40, 50%, 50%) 70%, hsl(40, 45%, 48%) 80%, hsl(40, 40%, 45%) 90%) 10;
       -o-border-image: -o-radial-gradient(circle, hsl(40, 50%, 50%) 70%, hsl(40, 45%, 48%) 80%, hsl(40, 40%, 45%) 90%) 10;
          border-image: radial-gradient(circle, hsl(40, 50%, 50%) 70%, hsl(40, 45%, 48%) 80%, hsl(40, 40%, 45%) 90%) 10;
}

.placeholder-text {
  margin: 0 auto;
  color: white;
  font-size: 5em;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}



