.Game-table {
  table-layout: fixed;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 75%;
  height: 75%;
  border-spacing: 0;
}

.Game-table tr :nth-child(2) {
  border-left: 0.08em solid white;
  border-right: 0.08em solid white;
}

.Game-table tbody :nth-child(2) td {
  border-top: 0.08em solid white;
  border-bottom: 0.08em solid white;
}

.Game-table tr :nth-child(2),
.Game-table tbody :nth-child(2) td {
  -webkit-border-image: -webkit-gradient(linear, left top, right bottom, from(hsl(0, 0%, 90%)), to(hsl(0, 0%, 75%))) 10 10 stretch;
  -webkit-border-image: -webkit-linear-gradient(top left, hsl(0, 0%, 90%), hsl(0, 0%, 75%)) 10 10 stretch;
       -o-border-image: -o-linear-gradient(top left, hsl(0, 0%, 90%), hsl(0, 0%, 75%)) 10 10 stretch;
          border-image: -webkit-gradient(linear, left top, right bottom, from(hsl(0, 0%, 90%)), to(hsl(0, 0%, 75%))) 10 10 stretch;
          border-image: linear-gradient(to bottom right, hsl(0, 0%, 90%), hsl(0, 0%, 75%)) 10 10 stretch;
}

.Game-table td {
  height: 33%;
  width: 33%;
  font-size: 11vmin;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media (max-width: 740px), (max-height: 740px) {
  .Game-table td {
    font-size: 8em;
  }
}




